@import url('https://fonts.googleapis.com/css2?family=Inter:wght@100..900&family=Marck+Script&family=Montserrat:ital,wght@0,100..900;1,100..900&family=Nunito:ital,wght@0,200..1000;1,200..1000&family=Oswald:wght@200..700&family=Roboto:ital,wght@0,100;0,300;0,400;0,500;0,700;0,900;1,100;1,300;1,400;1,500;1,700;1,900&family=Sora:wght@100..800&family=Tenor+Sans&display=swap');

body {
  background-color: rgb(58, 69, 75);
  color: rgb(255, 202, 40);
  font-family: "Montserrat", sans-serif;
}

.App {
  margin-top: 15px;
  margin-left: 75px;
  font-size: 22px;
  width: 85wh;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  overflow: hidden;
}

.logo {
  color: rgba(38, 50, 56, 0.266);
  font-size: 400px;
  position: fixed;
  bottom: -300px;
  right: 0px;
  cursor: default;
  z-index: -1000;
}

.header h1 {
  cursor: default;
  font-size: 44px;
}

.header h1 span {
  color: #fff;
}

.question-element {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  cursor: default;
}

.hide {
  display: none;
}

.answers-buttons {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  gap: 25px;
}

.answers-buttons button {
  text-align: start;
  padding-left: 15px;
  height: 60px;
  width: 450px;
  border: 0px;
  border-radius: 5px;
  font-family: "Montserrat", sans-serif;
  font-size: 20px;
  color: rgb(255, 255, 255);
  background-color: rgb(38, 50, 56);
  &:hover {
    cursor: pointer;
    opacity: 0.7;
  }
}

.progress {
  background-color: rgb(38, 50, 56); 
  width: 500px;
  height: 22px; 
  border-radius: 17px;
  display: flex;
  align-items: center;
}

.show-progress {
  background-color: rgb(255, 202, 40); 
  width: 50px;
  height: 20px; 
  border-radius: 15px;
  outline: 3px solid rgb(38, 50, 56);
  outline-offset: -2px;
  transition: width 1s ease-out 0s;
}

.resultBlock {
  display: flex;
}

.resultHeader {
  background-color: rgb(38, 50, 56);
  color: #fff;
}

.resultHeader, .resultTR {
  margin: 0;
  padding: 0;
  height: 40px;
  display: flex;
  align-items: center;
  padding-right: 30px;
  padding-left: 10px;
}

.resultTR:nth-child(odd) {
  background-color: rgb(76, 80, 83);
  /* color: #fff; */
}

.restartButton {
  margin-top: 20px;
  margin-bottom: 20px;
}


@media(max-width: 1200px) {
  .App {
    /* margin-top: 15px; */
    margin-left: calc(15px + (75 - 15) * ((100vw - 320px) / (1200 - 320)));
    font-size: 22px;
    width: calc(95vw + (85 - 95) * ((100vw - 320px) / (1200 - 320)));
  }
  
  .logo {
    display: none;
  }

  .header h1 {
    font-size: calc(25px + (44 - 25) * ((100vw - 320px) / (1200 - 320)));
  }

  .answers-buttons p {
    margin: 0px;
    height: 75px;
  }

  .answers-buttons {
    margin: 0px;
    padding: 0px;
    gap: 0px;
    margin-bottom: 20px;
    margin-right: 15px;
  }

  .answers-buttons button {
    padding-left: 15px;
    height: 60px;
    width: 95%;
    margin-bottom: 20px;
  }

  .resultHeader, .resultTR {
    font-size: calc(10px + (22 - 10) * ((100vw - 320px) / (1200 - 320)));
  }

  .resultText {
    /* font-size: calc(17px + (22 - 17) * ((100vw - 320px) / (1200 - 320))); */
    height: 40px !important;
    display: flex;
    align-items: center;
  }

  .progress {
    background-color: rgb(38, 50, 56); 
    width: calc(300px + (500 - 300) * ((100vw - 320px) / (1200 - 320)));
    
    border-radius: calc(10px + (17 - 10) * ((100vw - 320px) / (1200 - 320)));
    display: flex;
    align-items: center;
  }
}


